    max-height: 240px;
    padding: 10px;
    border: 2px solid #00ddff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    border-radius: 8px;
    cursor: pointer;
    transition: all .5s ease-in-out;

    .hed__contaiiner{
        display: flex;
        justify-content: center;
        align-items: baseline;

        h2{
            font-size: 40px;
            font-weight: 400;        
        }
    }

    .main__aboutMe{
        font-size: 18px;
        font-weight: 300;
        padding-top: 10px;
    }
}


.about__meContainner:hover{
