


.top__noscroll{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
    padding: 15px;
    background-color: #23272f;
    border-bottom: 2px solid #3c4352;

    .logo{
        font-size: 31px;
        a{
            color: aliceblue;
            text-decoration: none;
            font-weight: 700;
            transition: all .5s ease-in-out;
        }
        a:hover{
            color: #FFFFFF;
            text-shadow: 11px 0px 9px rgba(0,255,255,0.68);
        }
    }

    .nav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 12px;

        a{
            text-decoration: none;
            color: white;
            background-color: #00ddff;
            font-weight: 300;
            padding: 5px;
            border-radius: 8px;
            transition: all .5s ease-in-out;
        }

        a:hover{
            padding: 7px;
            background-color: #00ffff;
            -webkit-box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
            -moz-box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
            box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
        }
    }

    .aut{
        a{
            background-color: #00ddff;
            padding: 5px;
            border-radius: 8px;
            transition: all .5s ease-in-out;
            text-decoration: none;
            color: white;
            font-weight: 300;
        }

        a:hover{
            padding: 7px;
            background-color: #00ffff;
            -webkit-box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
            -moz-box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
            box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
        }
    }
}