

.error__contaiiner{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 10%;
    margin: 0 auto;
    border: 2px solid #00ddff;
    min-width: 349px;
    min-height: 230px;
    border-radius: 8px;
    cursor: pointer;
    transition: all .5s ease-in-out;

    .error__wrap{
        font-size: 67px;
    }
}

.error__contaiiner:hover{
    margin-top: 10%;
    margin: 0 auto;
    min-width: 367px;
    min-height: 256px;
    background-color: #00ddff;
}

