.containner__post{
    width: 89vw;
    max-height: 100vh;
    border: 2px solid #00ddff;
    padding: 5px;
    border-radius: 8px;
    transition: all .5s ease-in-out;
    cursor: pointer;

    .line__top{
        display: flex;
        align-items: center;
        justify-content: space-around;

        .zag__post{
            font-size: 34px;
            font-weight: 500;
        }

        span{
            font-size: 20px;
            font-weight: 300;
        }
    }

    .contetn__noscroll{
        padding: 20px;

        span{
            font-weight: 300;
            font-size: 18px;
        }
    }
}

.containner__post:hover{
    background-color: #00ffff;
    padding: 23px;
}