.prog__modcont{
  width: 78vw;
  max-height: 100%;
  border: 2px solid #00ddff;
  margin: 0 auto;
  padding: 10px;
  border-radius: 8px;
  transition: all .5s ease-in-out;
  cursor: pointer;

  .zag__prog{
    width: 100%;
    border-bottom: 2px solid #00ddff ;

    h1{
      font-size: 25px;
      font-weight: 400;
    }
  }

  .prog__text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    span{
      font-weight: 300;
      font-size: 16px;
    }
  }
}

.prog__modcont:hover{
  padding: 18px;
  background-color: #00ffff ;
}