

.about__mainContainner{
    margin: 0 auto;
    max-width: 1000px;
    max-height: 240px;
    padding: 10px;
    border: 2px solid #00ddff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    border-radius: 8px;
    transition: all .5s ease-in-out;

    .hed__cont{
        display: flex;
        align-items: center;
        justify-content: center;

        span{
            font-size: 18px;
            display: flex;
            text-align: center;
        }
    }
}




.about__mainContainner:hover{
    padding: 23px;
    background-color: #00ffff
}