:is(a, h1, h2, h3, span){
    color: white;
    text-decoration: none;
}

.prewu__containner{
    position: relative;
    margin: 0 auto;
    flex-direction: column;
    margin-top:10%;
    max-height: 345px;
    min-height: 200px;
    max-width: 500px;
    min-width: 350px;
    border: 2px solid #00ddff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    cursor: pointer;
    transition: all .5s ease-in-out;

    .prewiu__footer{
        display: flex;
    }

    .prewiu__main{
        display: flex;
        justify-content: center;
        align-items: center;

        h1{
            font-size: 56px;
            position: relative;
            transition: all .6s ease-in-out;
        }

        span{
            position: absolute;
            bottom: 56px;
            top: 89px;
            margin-bottom: 56px;
        }
    }
}

.prewu__containner:hover{
    margin-top: 12%;
    max-height: 350px;
    min-height: 235px;
    max-width: 550px;
    min-width: 375px;
    background-color: #00ffff;
    font-size: 60px;
    -webkit-box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
    -moz-box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
    box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);

    h1{
        font-size: 79px;
    }
}