.tec__prewcontainner{
    margin: 0 auto;
    max-width: 1100px;
    height: 156px;
    margin-top: 24%;
    padding: 20px;
    background-color: #00ddff;
    border-radius: 8px;
    cursor: pointer;
    transition: all .5s ease-in-out;

    .line__tec{
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .tec__item{
            height: 100%;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;

            h3{
                font-size: 34px;
                font-weight: 400;
            }
        }
    }
}

.tec__prewcontainner:hover{
    padding: 34px;
    -webkit-box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
    -moz-box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
    box-shadow: -2px 5px 20px 9px rgba(0, 255, 255, 0.26);
}